export const fetchDeleteContacts = createAsyncThunk(
   'contact/delete',
   async(id ,thunkApi)=>{
      try{
          await api.deleteContacts(id)
         return id
      }
      catch({response}){
         return thunkApi.rejectWithValue(response.data)
      }
   }
)